<template>
  <div class="paymentCode">
    <div class="bg">
      <img src="../../assets/img/pic/bg6.png" alt />
    </div>
    <div class="mid">
      <div class="barCode">
        <div v-if="$route.query.type == '1'">
          <img :src="$utils.dealImg(detail.shopHeader)" alt="">
          <p class="text">{{detail.shopName}}</p>
        </div>
        <div v-else>
          <img style="border-radius:50%;" :src="$utils.dealImg(detail.userHeader)" alt="">
          <p class="text">{{detail.userName}}</p>
        </div>
      </div>
      <div class="qrCode">
        <img :src="detail.qrCode" alt />
        <!-- <p>每分钟自动刷新，限当前使用</p> -->
      </div>
      <!-- <div class="bottom" @click="$router.push({name:'cardVoucher'})">
        <img class="bottom-left" src="../../assets/img/pic/1.png" alt />
        <p class="bottom-mid">我的卡券</p>
        <img class="bottom-right" src="../../assets/img/pic/3.png" alt />
      </div> -->
    </div>
  </div>
</template>
<script>
import { getBusinessCode, getPersonCode } from "@/api";
export default {
  data() {
    return {
      detail: {}
    };
  },
  methods: {
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      if (this.$route.query.type == "1") {
        getBusinessCode({
          qrurl: this.$config.host2 + "/wx/#/pay?type=1&shopCode="
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.detail = res.data;
          } else {
            this.$toast(res.msg);
            setTimeout(() => {
              this.$router.back();
            }, 1000);
          }
        });
      } else {
        getPersonCode({
          qrurl: this.$config.host2 + "/wx/#/pay?type=2&userCode="
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.detail = res.data;
          } else {
            this.$toast(res.msg);
          }
        });
      }
    }
  },
  mounted() {
    document.title = "付款码";
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.paymentCode {
  .bg {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .mid {
    background: url("../../assets/img/pic/bg3.png") no-repeat;
    background-size: 100% 100%;
    position: relative;
    z-index: 1;
    text-align: center;
    margin: 0 30px;
    height: 75vh;
    padding: 6vh 0 0;
    top: 30px;
    .barCode {
      // padding: 0 0 5px;
      // border-bottom: 1pt solid #efefef;
      img {
        // width: 80%;
        height: 23vh;
      }
      .text {
        font-size: 15px;
        color: #333;
        line-height: 25px;
      }
    }
    .qrCode {
      margin: 5px 0 0;
      img {
        width: 50%;
      }
      p {
        font-size: 12px;
        color: #999;
        line-height: 25px;
      }
    }
    .bottom {
      background: url("../../assets/img/pic/4.png") no-repeat;
      background-size: 100% 100%;
      width: 95%;
      height: 60px;
      margin: 8vh auto 0;
      display: flex;
      align-items: center;
      &-left {
        width: 30px;
        margin: 0 10px 0 30px;
      }
      &-mid {
        flex: 2;
        color: #fff;
        line-height: 60px;
        font-size: 15px;
        text-align: left;
      }
      &-right {
        width: 8px;
        margin: 0 30px 0 0;
      }
    }
  }
}
</style>
